
import { defineComponent, ref } from 'vue';
import { QuickFetch } from '@/components/quick'
import { useRouter } from 'vue-router'

export default defineComponent({
    setup() {
        const account: any = ref("");
        const password: any = ref("");
        const qf = new QuickFetch();
        const router = useRouter();
        function submitAll(){
            if(qf.EptObj(account)){
                alert("請輸入帳號");
            }else if(qf.EptObj(password)){
                alert("請輸入密碼")
            }else{
                qf.Dataset().AddObj('account', account).AddObj('password',password);
                qf.Url('auth/login').Post(qf.GetDset()).then((res: any)=>{
                    if(res.message=='done'){
                        localStorage.nhc_admin_token = res.token.token;
                        localStorage.nhc_admin_data = JSON.stringify(res.token);
                        router.push('/');
                    }else{
                        alert("登入失敗，請洽系統管理員");
                    }
                });
            }
        }
        return {
            account,
            password,
            submitAll,
        }
    }
});
